@import url(https://fonts.googleapis.com/css?family=Lobster&subset=cyrillic);
body {
  background: #282c37;
  margin: 0;
}
button:focus {
  outline: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  color: #fff;
}
#svg-liquid {
  position: absolute;
  top: -999px;
  visibility: hidden;
}

canvas {
  -webkit-filter: url(#liquid);
          filter: url(#liquid);
  pointer-events: none;
}
#refresh {
  height: 100vh;
  display: grid;
  justify-items: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#refresh button {
    -webkit-box-shadow: 0px 10px 14px -7px #05a88a;
    box-shadow: 0px 10px 14px -7px #05a88a;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #02bd9b), to(#15dbb7));
    background: linear-gradient(to bottom, #02bd9b 5%, #15dbb7 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#02bd9b', endColorstr='#15dbb7',GradientType=0);
    background-color: #02bd9b;
    border-radius: 4px;
    border: 1px solid #18c9a9;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 16px;
    font-weight: bold;
    padding: 8px 22px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #00a688;
}
#refresh button:hover {
      background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #15dbb7), to(#02bd9b));
      background: linear-gradient(to bottom, #15dbb7 5%, #02bd9b 100%);
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#15dbb7', endColorstr='#02bd9b',GradientType=0);
      background-color: #15dbb7;
}
#refresh button:active {
      position: relative;
      top: 1px;
}
#explode {
  padding: 30px 0;
  position: relative;
  min-height: 100vh;
}
#cards {
  max-width: 80vw;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 50px;
  grid-row-gap: 50px;
  justify-items: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#cards .card {
    width: 100%;
    color: #282c37;
}
#cards .card:hover section.thumb {
      -webkit-transform-origin: 100% 100%;
              transform-origin: 100% 100%;
      -webkit-transform: perspective(500px) rotateY(10deg) rotateX(0deg) scale(1.05);
              transform: perspective(500px) rotateY(10deg) rotateX(0deg) scale(1.05);
}
#cards .card:hover section.thumb::after {
        opacity: 1;
}
#cards .card:hover section.btn-group button {
      -webkit-transform: translateY(-25px);
              transform: translateY(-25px);
      opacity: 1;
}
#cards .card:hover section.btn-group button:nth-of-type(2) {
        -webkit-transition-delay: 50ms;
                transition-delay: 50ms;
}
#cards .card:hover section.btn-group button:nth-of-type(1) {
        -webkit-transition-delay: 100ms;
                transition-delay: 100ms;
}
#cards .card:hover section.btn-group button:nth-of-type(0) {
        -webkit-transition-delay: 150ms;
                transition-delay: 150ms;
}
#cards .inner {
    overflow: hidden;
    border-radius: 3px;
    -webkit-box-shadow: 0 12px 30px -10px black;
            box-shadow: 0 12px 30px -10px black;
    display: grid;
    grid-template-rows: minmax(150px, calc(50vh - 140px)) auto;
}
#cards section.thumb {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    -webkit-transform-origin: 50% 30%;
            transform-origin: 50% 30%;
    -webkit-transform: perspective(500px) rotateX(0deg);
            transform: perspective(500px) rotateX(0deg);
}
#cards section.thumb::after {
      content: '';
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      -webkit-box-shadow: 0 25px 40px -30px #000;
              box-shadow: 0 25px 40px -30px #000;
      -webkit-transition: 0.3s;
      transition: 0.3s;
      opacity: 0.1;
}
#cards section.meta {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-column-gap: 10px;
    background: #f4f7fa;
    padding: 16px 12px;
}
#cards section.meta img.avatar {
      border-radius: 50%;
      height: 40px;
}
#cards section.meta .title {
      font-weight: bold;
}
#cards section.meta .title:first-letter {
        text-transform: capitalize;
}
#cards section.meta .author {
      font-size: 0.8em;
      font-style: italic;
}
#cards section.btn-group {
    height: 0;
    text-align: right;
}
#cards section.btn-group button {
      -webkit-transition: opacity 0.2s, -webkit-transform 0.3s, -webkit-box-shadow 0.2s;
      transition: opacity 0.2s, -webkit-transform 0.3s, -webkit-box-shadow 0.2s;
      transition: transform 0.3s, opacity 0.2s, box-shadow 0.2s;
      transition: transform 0.3s, opacity 0.2s, box-shadow 0.2s, -webkit-transform 0.3s, -webkit-box-shadow 0.2s;
      -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
              transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
      opacity: 0;
      cursor: pointer;
      font-size: 1.5em;
      height: 40px;
      width: 40px;
      border-radius: 50%;
      margin-right: 15px;
      -webkit-box-shadow: 0 2px 15px -5px #000, inset 0 -1px 0 1px rgba(0, 0, 0, 0.2);
              box-shadow: 0 2px 15px -5px #000, inset 0 -1px 0 1px rgba(0, 0, 0, 0.2);
      color: #fff;
      background: #28bd8b;
      border: none;
}
#cards section.btn-group button:hover {
        background: #1f936c;
}
#cards section.btn-group button.remove {
        background: #ea5a5a;
}
#cards section.btn-group button.remove:hover {
          background: #e42d2d;
}
@media (max-width: 46.24em) {
#cards {
      grid-template-columns: 1fr;
}
#cards .inner {
        grid-template-rows: calc(100vh - 150px);
}
#cards .thumb {
        height: 100%;
}
}
@media (min-width: 46.25em) {
#cards {
      grid-template-columns: 1fr 1fr;
}
}
@media (min-width: 81.25em) {
#cards {
      grid-template-columns: 1fr 1fr 1fr;
}
}
.avf-transition-leave-active {
  position: absolute; }

.avf-transition-move {
  -webkit-transition: 0.5s cubic-bezier(0.8, 0, 0, 1.1);
  transition: 0.5s cubic-bezier(0.8, 0, 0, 1.1); }
